const uni = require('uni').default;

















export default {
	data: {
		title: 'list-triplex-row',
		banner: {},
		htmlString: ""
	},
	onShareAppMessage() {
		return {
			title: this.banner.title,
			path: '/pages/template/list2detail-detail/list2detail-detail?detailDate=' + JSON.stringify(this.banner)
		}
	},
	onLoad(e) {
		this.banner = JSON.parse(e.detailDate);
		this.getDetail();
		uni.setNavigationBarTitle({
			title: this.banner.title
		})
	},
	onUnload() {
		this.htmlString = "";
		this.banner = {}
	},
	methods: {
		getDetail() {
			uni.request({
				url: 'https://spider.dcloud.net.cn/api/news/36kr/' + this.banner.post_id,
				success: (data) => {
					if (data.statusCode == 200) {
						this.htmlString = data.data.content.replace(/\\/g, "").replace(/<img/g,"<img width='100%'");
					}
				},
				fail: () => {
					console.log('fail');
				}
			})
		}
	}
}



